import { useCallback, useEffect, useMemo, useState } from "react";

type MomentStatus = "needs_review" | "approved" | "rejected";

type RawMoment = {
  id: number | string;
  content_title?: string;
  season?: number | null;
  episode?: number | null;
  minute?: number | null;
  confidence?: number | null;
  quote?: string | null;
  source_subreddit?: string | null;
  source_url?: string | null;
  created_utc?: number | null;
  status?: MomentStatus | string | null;
};

export interface MomentData {
  id: number;
  content_title: string;
  season?: number | null;
  episode?: number | null;
  minute?: number | null;
  confidence: number;
  quote?: string | null;
  source_subreddit?: string | null;
  source_url?: string | null;
  created_utc?: number | null;
  status: MomentStatus;
}

export function normalizeMoment(raw: RawMoment | null | undefined): MomentData | null {
  if (!raw) return null;

  const numericId = typeof raw.id === "number" ? raw.id : Number(raw.id);
  if (!Number.isFinite(numericId)) return null;

  const title = typeof raw.content_title === "string" && raw.content_title.trim().length > 0
    ? raw.content_title
    : "Untitled moment";

  const confidence = typeof raw.confidence === "number" ? raw.confidence : 0;
  const status: MomentStatus = raw.status === "approved" || raw.status === "rejected"
    ? (raw.status as MomentStatus)
    : "needs_review";

  return {
    id: numericId,
    content_title: title,
    season: raw.season ?? null,
    episode: raw.episode ?? null,
    minute: raw.minute ?? null,
    confidence,
    quote: raw.quote ?? null,
    source_subreddit: raw.source_subreddit ?? null,
    source_url: raw.source_url ?? null,
    created_utc: raw.created_utc ?? null,
    status,
  };
}

export function createPendingMomentsController(fetchImpl: typeof fetch) {
  let moments: MomentData[] = [];
  let error: string | null = null;

  const getMoments = () => moments;
  const getError = () => error;

  const load = async () => {
    try {
      const response = await fetchImpl("/api/moderation/pending");
      if (!response.ok) throw new Error(`Request failed with status ${response.status}`);
      const result = await response.json();
      moments = Array.isArray(result?.data)
        ? (result.data
            .map((item: RawMoment) => normalizeMoment(item))
            .filter((value): value is MomentData => value !== null))
        : [];
      error = null;
    } catch (err) {
      error = err instanceof Error ? err.message : "Failed to fetch data";
      console.error("Error fetching pending moments", err);
      throw err;
    }
    return moments;
  };

  const remove = (id: number) => {
    moments = moments.filter((moment) => moment.id !== id);
    return moments;
  };

  const postAction = async (path: string, id: number) => {
    return fetchImpl(path, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ id }),
    });
  };

  const approve = async (id: number | string) => {
    const numericId = typeof id === "number" ? id : Number(id);
    const response = await postAction("/api/moderation/approve", numericId);
    if (response.ok) remove(numericId);
    return response;
  };

  const reject = async (id: number | string) => {
    const numericId = typeof id === "number" ? id : Number(id);
    const response = await postAction("/api/moderation/reject", numericId);
    if (response.ok) remove(numericId);
    return response;
  };

  return {
    load,
    approve,
    reject,
    remove,
    getMoments,
    getError,
  } as const;
}

export function usePendingMoments() {
  const controller = useMemo(() => createPendingMomentsController(fetch), []);
  const [moments, setMoments] = useState<MomentData[]>(controller.getMoments());
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(controller.getError());

  const refresh = useCallback(async () => {
    try {
      setLoading(true);
      const data = await controller.load();
      setMoments([...data]);
      setError(null);
    } catch (err) {
      const message = err instanceof Error ? err.message : "Failed to fetch data";
      setError(message);
    } finally {
      setLoading(false);
    }
  }, [controller]);

  useEffect(() => {
    refresh();
  }, [refresh]);

  const approveMoment = useCallback(async (id: number) => {
    const response = await controller.approve(id);
    if (response.ok) setMoments([...controller.getMoments()]);
    return response;
  }, [controller]);

  const rejectMoment = useCallback(async (id: number) => {
    const response = await controller.reject(id);
    if (response.ok) setMoments([...controller.getMoments()]);
    return response;
  }, [controller]);

  return {
    moments,
    loading,
    error,
    refresh,
    approveMoment,
    rejectMoment,
  } as const;
}
